import React from 'react'
import worldBook from "../Images/worldBook.jpg"
import lavishLook from "../Images/lavishLook.jpg"
import monday from "../Images/monday.jpg"
import fitfactory from "../Images/fitfactory.jpg"
import { Badge, Button, Heading ,Text} from '@chakra-ui/react'
import "./project.css"
import { Link } from 'react-router-dom'
let data=[
  {id:1,name:"World Book",img:worldBook,link:"https://poetic-griffin-07f79c.netlify.app/",git:"https://github.com/Sachin-Kesarwani/smooth-cracker-7739",
   description:"This website generally for to buy books , the main reason behind to built this website is make students life easy and relaxe .In this project there are many functionality just lik signup , login ,Add to cart ,paymnet scrtion etc . Happy to share that it is the first step toward coding . ",tech:"HTML |  CSS | JavaScript"},
  {id:2,name:"Lavish Look",img:lavishLook,link:"https://lavish-look-sachin-kesarwani.vercel.app/",git:"https://github.com/Sachin-Kesarwani/pushy-dime-8312",
description:"This is E-commerce websit where you can buy clothes , shoes and products for kids . In this website there are many types product for both mens , womens and for childrens. If we talk about in perspective of functionality it have almost all functionality just Add to cart , Buy , Payment Section , Login ,Signup and Admin side also ",tech:"REACT | Chakra UI | Bootstrap"},
  {id:3,name:"monday.com",img:monday,link:"https://lovely-snickerdoodle-8bb2d5.netlify.app/",git:"https://github.com/lokeshchoudharyprogrammer/superficial-point-8316",tech:"HTML |  CSS | JavaScript",
description:"This is a collaborative which is cloned by 5 developers . Basically This website is just for to make team ,assign member for team . In this project we added many functionality like signup , login , Add users , Assigning work panel and Team making panel"}, 
{id:4,name:"Fit-Factory",img:fitfactory,link:"https://fit-factory.vercel.app/",git:"https://github.com/Sachin-Kesarwani/detailed-hour-2985",tech:"React |  Chakra UI | Redux | React-Redux | Redux-thunk  ",
description:"This is a collaborative which is cloned by 3 developers . Basically This is the Indias No 1website where we sell health realted products ."}
]
const Project = () => {
  return (
    <div  id="projects">
      {/* <div id="projectmaindiv"  > */}
        {
          data.map((e)=>{
            return <div   className="project-card" >
                <div  >
                  <Heading as={"h4"} color={"red.800"} className="project-title">{e.name}</Heading>
                  <img src={e.img} />
                </div>
                <hr style={{border:"1px solid black"}}></hr>
                <div style={{padding:"8px"}}>
                 <Text  className="project-description">{e.description}</Text>

                </div>

                <hr style={{border:"1px solid black"}}></hr>
                <div className="project-tech-stack">
               <Text >
                <Badge bg="blue.400" color={"white"} >
                Tech Stack
                </Badge>
               : {e.tech}</Text>
                </div>
                <hr style={{border:"1px solid black"}}></hr>
                <div>
                  <Button style={{position: "static"}}  className="project-deployed-link" bg="orange.600" margin={1} color="white" _hover={{bg:"blue.500"}}  ><a href={e.link}  target="_blank">Live </a></Button>
                  <Button style={{position: "static"}} className="project-github-link" bg="orange.600" color="white" _hover={{bg:"blue.500"}}  ><a href={e.git}  target="_blank">Github</a></Button>
                </div>
            </div>
          })
        }
      {/* </div> */}
    </div>
  )
}

export default Project
