import { Button, Heading } from '@chakra-ui/react'
import React from 'react'
import resume from "../Images/resume.pdf"
const Resume = () => {
  function redirect(){
    window.open("https://drive.google.com/file/d/1zztPdp7ZZE-dTh7X4-V7oOVyuHH7TVXH/view","_blank","noreferrer")
  }
  return (
    <>

    <div style={{width:"100%",padding:"30px",border:"1px solid red" ,backgroundColor:"skyblue"}}>
      <Heading  color="red.900">Resume</Heading>
      <Button style={{  position: "static",marginTop:"20px"}}  onClick={redirect} bg="red.600"  color="white" _hover={{bg:"blue.500"}}  id="resume-button-1" >
      <a 
       href={resume} 
       id="resume-link-1"  download="Sachin-Kesarwani.pdf" >
            
             Resume
        
             
           
              </a>
              </Button>
    </div>
    </>
   
  )
}

export default Resume
