import mypic from "./mypic.png";
import resume from "../Images/resume.pdf";
import {
  Box,
  Flex,
  Avatar,
  HStack,
  IconButton,
  Button,
  Menu,
  MenuButton,
  useDisclosure,
  useColorModeValue,
  Stack,
  Heading,
} from "@chakra-ui/react";
import { HamburgerIcon, CloseIcon } from "@chakra-ui/icons";
import { HashLink as Link } from "react-router-hash-link";

let data = [
  { path: "#home", title: "Home", class: "nav-link home" },

  { path: "#project", title: "Project", class: "nav-link projects" },
  { path: "#contact", title: "Contact", class: "nav-link contact" },
  { path: "#about", title: "About", class: "nav-link about" },
  { path: "#skills", title: "Skill", class: "nav-link skills" },
];

// {path:"#resume",title:"Resume",class:"nav-link resume"}

export default function Simple() {
  const { isOpen, onOpen, onClose } = useDisclosure();
  // const NavLink = ({ children }) => (
  //   <Link
  //     px={2}
  //     py={1}
  //     rounded={"md"}
  //     _hover={{
  //       textDecoration: "none",
  //       bg: useColorModeValue("gray.200", "gray.700"),
  //     }}
  //     href={"#"}
  //   >
  //     {children}
  //   </Link>
  // );
  function redirect() {
    window.open(
      "https://drive.google.com/file/d/19XjhZLP1YYOF43HV7AVQY6yY5ck-Jnpj/view?usp=share_link",
      "_blank",
      "noreferrer"
    );
  }
  return (
    <>
      <Box
        id="nav-menu"
        bg={useColorModeValue("blue.100", "blue.900")}
        position={"fixed"}
        w="100%"
        px={4}
      >
        <Flex h={16} alignItems={"center"} justifyContent={"space-between"}>
          <IconButton
            size={"md"}
            gr
            icon={isOpen ? <CloseIcon /> : <HamburgerIcon />}
            aria-label={"Open Menu"}
            display={{ md: "none" }}
            onClick={isOpen ? onClose : onOpen}
          />
          <HStack spacing={8} alignItems={"center"}>
            <Heading>LC</Heading>
            <HStack
              as={"nav"}
              spacing={4}
              display={{ base: "none", md: "flex" }}
            >
              {data.map((e) => (
                <Link
                  spy={true}
                  smooth={true}
                  offset={50}
                  duration={500}
                  to={e.path}
                  className={e.class}
                >
                  <Button
                    bg="red.600"
                    color="white"
                    _hover={{ bg: "blue.500" }}
                  >
                    {e.title}
                  </Button>
                </Link>
              ))}
              <Link
                className="nav-link resume"
                to={resume}
                id="resume-link-1"
                target="_blank"
                download="Lokesh-Choudhary.pdf"
              >
                <Button
                  onClick={redirect}
                  bg="red.600"
                  color="white"
                  _hover={{ bg: "blue.500" }}
                  id="resume-button-1"
                >
                  Resume
                </Button>
              </Link>
            </HStack>
          </HStack>
          <Flex alignItems={"center"}>
            <Menu>
              <MenuButton
                as={Button}
                rounded={"full"}
                variant={"link"}
                cursor={"pointer"}
                minW={0}
              >
                <Avatar size={"sm"} src={mypic} />
              </MenuButton>
              {/* <MenuList>
                <MenuItem>Link 1</MenuItem>
                <MenuItem>Link 2</MenuItem>
                <MenuDivider />
                <MenuItem>Link 3</MenuItem>
              </MenuList> */}
            </Menu>
          </Flex>
        </Flex>

        {isOpen ? (
          <Box pb={4} display={{ md: "none" }}>
            <Stack as={"nav"} spacing={4}>
              {data.map((e) => (
                <Link
                  spy={true}
                  smooth={true}
                  offset={50}
                  duration={500}
                  w="40px"
                  to={e.path}
                  className={e.class}
                >
                  {e.title}
                </Link>
              ))}
              <Link
                className="nav-link resume"
                // href={resume}
                to={resume}
                // to={"#resume"}
                id="resume-link-1"
                download="Lokesh-Choudhary.pdf"
              >
                <Button
                  onClick={redirect}
                  bg="red.600"
                  color="white"
                  _hover={{ bg: "blue.500" }}
                  id="resume-button-1"
                >
                  Resume
                </Button>
              </Link>
            </Stack>
          </Box>
        ) : null}
      </Box>
    </>
  );
}
